<p><span class="rvts26">主派系</span></p>
</td>
<td width="173" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">默认技能</span><span class="rvts27">1</span></p>
</td>
<td width="173" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">默认技能</span><span class="rvts27">2</span></p>
</td>
<td width="173" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">默认技能</span><span class="rvts27">3</span></p>
</td>
</tr>
<tr valign="top">
<td width="178" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
